<template>
  <div class="index clearfix">
    <div class="index-content">
      <ul class="index-content-ul">
        <li v-for="(item,i) in listData" :key="i" @click="turnToDetails(item._id)">
          <el-row>
            <el-col :span="18">
              <div class="index-row-info">
                <span class="index-row-type">{{getTypeName(item.type)}}</span>
                <span class="index-row-name">oldzhao</span>
                <span class="index-row-time">{{getTime(item.time)}}</span>
              </div>
              <div class="index-row-title">{{item.title}}</div>
              <div class="index-row-num">
                <span class="index-row-support">
                  <img
                    src="https://b-gold-cdn.xitu.io/v3/static/img/zan.e9d7698.svg"
                    alt
                    class="index-num-images"
                  >
                  <span class="index-num-numbers">1</span>
                </span>
                <span class="index-row-msg">
                  <img
                    src="https://b-gold-cdn.xitu.io/v3/static/img/comment.4d5744f.svg"
                    alt
                    class="index-num-images"
                  >
                  <span class="index-num-numbers">2</span>
                </span>
              </div>
            </el-col>
            <el-col :span="6">
              <img :src="getImages(item.imgId)" alt="img" class="index-row-img">
            </el-col>
          </el-row>
        </li>
      </ul>
    </div>
    <div class="index-sidebar">
      <el-row class="index-register">
        <el-col :span="24">
          <el-input v-model="account" placeholder="请输入账号" size="medium"></el-input>
        </el-col>
        <el-col :span="24">
          <el-input v-model="password" placeholder="请输入密码" size="medium" type="password"></el-input>
        </el-col>
        <el-col :span="24">
          <el-input v-model="repassword" placeholder="请再输入密码" size="medium" type="password"></el-input>
        </el-col>
        <el-col :span="24">
          <el-button type="primary" size="medium" class="index-register-btn" @click="register">立即注册</el-button>
        </el-col>
      </el-row>
      <div class="index-login">
        <span class="index-login-title">第三方登录：</span>
        <img src="https://b-gold-cdn.xitu.io/v3/static/img/github.547dd8a.svg" alt="github">
      </div>
    </div>
  </div>
</template>

<script>
import { getArticleList } from "@/api/index";
import { registerUser } from "@/api/user";
import { filterObj, filterFormatTime } from "@/utils/filter";

export default {
  name: "index",
  data() {
    return {
      account: "",
      password: "",
      repassword: "",

      logo: require("@/assets/logo.png"),
      limit: 10,
      page: 1,
      tableParams: {
        limit: 0,
        page: 0
      },
      listData: [],
      totalCount: 0
    };
  },
  methods: {
    register() {
      if (!this.account) {
        this.$message.error("请输入账号！");
        return false;
      }

      if (!this.password) {
        this.$message.error("请输入密码！");
        return false;
      }

      if (!this.repassword) {
        this.$message.error("请再输入密码！");
        return false;
      }

      if (this.repassword != this.password) {
        this.$message.error("密码不一致");
        return false;
      }

      let data = {
        account: this.account,
        password: this.password
      };

      registerUser(filterObj(data)).then(res => {
        if (res.data.ret == 1) {
          this.$message.success("注册成功！");
         this.$emit('changeStatus');
         this.initData();
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    initData() {
      this.password = "";
      this.account = "";
      this.repassword = "";
    },
    turnToDetails(id) {
      window.open("http://localhost:8080/#/detail?id=" + id);
    },
    getImages(id) {
      return "http://localhost:3000/imgUp/file?id=" + id;
    },
    getTime(time) {
      return filterFormatTime(time);
    },
    getTypeName(type) {
      if (type == 1) {
        return "技术";
      } else if (type == 2) {
        return "生活";
      }
    },
    getList() {
      getArticleList(filterObj(this.tableParams)).then(res => {
        this.listData = res.data.list;
        this.totalCount = parseInt(res.data.count);
      });
    }
  },
  mounted() {
    this.tableParams.limit = this.limit;
    this.tableParams.page = this.page;

    this.getList();
  }
};
</script>

<style>
.index-content-ul li {
  width: 100%;
  box-sizing: border-box;
  padding: 5px 14px;
  margin: 10px 0;
  cursor: pointer;
}

.index-row-info {
  height: 16px;
  line-height: 16px;
  font-size: 14px;
}

.index-row-type {
  color: #b71ed7;
  font-weight: 500;
  margin-right: 10px;
}

.index-row-name,
.index-row-time {
  margin: 0 10px;
  color: #a2a2a2;
}

.index-row-title {
  font-size: 16px;
  font-weight: bold;
  color: #333333;
  margin: 10px 0;
}

.index-row-num {
  margin: 5px 0;
}

.index-row-support,
.index-row-msg {
  /* border: 1px solid #b2bac2; */
  line-height: 24px;
  height: 24px;
  padding: 0 5px;
  font-size: 16px;
  color: #a2a2a2;
}

.index-num-images,
.index-num-numbers {
  display: inline-block;
  vertical-align: middle;
}

.index-row-img {
  width: 80px;
  height: 80px;
  margin: 6px auto;
  display: block;
}

/* 右侧登录 注册 */
.index-sidebar {
  padding: 26px;
}

.index-register .el-col {
  margin-bottom: 10px;
}

.index-register .index-register-btn {
  width: 100%;
}

.index-login {
  height: 24px;
  line-height: 24px;
  font-size: 14px;
  color: #a2a2a2;
}

.index-login img,
.index-login .index-login-title {
  display: inline-block;
  vertical-align: middle;
}

.index-login img {
  width: 24px;
  margin-left: 10px;
  cursor: pointer;
}
</style>