import request from '../utils/request'

export function loginUser(data) {
    return request({
        method: 'POST',
        url: "/user/login",
        data
    })
}

export function registerUser(data) {
    return request({
        method: 'POST',
        url: "/user/register",
        data
    })
}

export function modPassword(data) {
    return request({
        method: 'POST',
        url: "/user/modPwd",
        data
    })
}

export function modName(data) {
    return request({
        method: 'POST',
        url: "/user/modName",
        data
    })
}

export function modImg(data) {
    return request({
        method: 'POST',
        url: "/user/modImg",
        data
    })
}

export function logout() {
    return request({
        method: 'POST',
        url: "/user/logout",
    })
}